
                    @use 'src/styles/breakpoints.scss' as *;
                    @use 'src/styles/variables.scss' as *;
                
.home {
    &__lead {
        padding-top: 200px;
        justify-content: flex-end;
        background:
            image-set(
                    url('./1xearth.png') 1x,
                    url('./2xearth.png') 2x,
                    url('./4xearth.png') 4x
                )
                no-repeat left 15px top -100px,
            linear-gradient($black-transparent, $grey-transparent),
            image-set(
                    url('./1xgalaxy.png') 1x,
                    url('./2xgalaxy.png') 2x,
                    url('./4xgalaxy.png') 4x
                )
                no-repeat center;
        background-size:
            auto 336px,
            100% 100%,
            cover;
    }

    &__text-button {
        align-self: flex-start;
    }
}

// The @include respond(md) mixin applies styles for medium screens
@include respond(md) {
    .home {
        &__lead {
            justify-content: center;
            // these paddings are based on the background image sizes, so they are in pixels
            padding-right: 260px;
            background-position:
                right -100px top 25px,
                0 0,
                center;
            background-size:
                auto 367px,
                100% 100%,
                cover;
        }
    }
}

// The @include respond(lg) mixin applies styles for large screens
@include respond(lg) {
    .home {
        &__lead {
            padding-right: 500px;
            background-position:
                right 90px top 60px,
                0 0,
                center;
            background-size:
                auto 367px,
                100% 100%,
                cover;
        }
    }
}
